<template>
  <div class="newUser" id="newUser">
     <!-- 银行卡部分 -->
     <!-- <div @click="User.convertJson()">sdfsdfsdf</div> -->
      <div class="bankCard bgfff icon pl12 bb pr" :class="{jt:this.bankCardInfoTotal.length > 1}" @click="showBank">   
        <div class="dis-inline ver-mid">
        <p></p>
          <img  :src='this.contextPath+"/static/bank-card-stage-static/assets/bank-logo/"+this.bankCardInfo.bankCode+".png"' width="30" height="30" alt="banklogo">
        </div>
        <div class="dis-inline ver-mid">
          <span class="fs16 c333">{{bankCardInfo.bankName}} </span>
        </div>
      </div>

      <div class="bankCard bgfff icon pl12 bb pr" :class="{jt:this.periodList[this.bankCardInfoTotal[0].bankCode].length > 1}"  @click="showStage">
        <div class="dis-inline fs16 stagetext">
          <span class="fs16 c666">分期期数</span>
        </div>
        <span class="stageNo fs16 c666">{{this.bankStage}}</span>期
      </div>
      <!-- 钱 -->
      <ul class="info bgfff pl12 c666 pt10 pb10">
          <li class="li">
            <span class="text">订单金额:</span>&nbsp;<i class="ver-t">￥</i><i class="orderMoney">{{this.orderAmount}}</i>
          </li>
          <li class="li">
            <span class="text">手续费:</span>&nbsp;<i class="ver-t">￥</i><i class="handleMoney">{{this.fee}}</i>
          </li>
           <li class="li" v-show="this.feeAfterSubsidy">
            <span class="text">减免后手续费:</span>&nbsp;<i class="ver-t cff491f">￥</i><i class="handleMoney cff491f">{{this.feeAfterSubsidy}}</i>
          </li>
          <li class="li">
            <span class="text">首期还款:</span>&nbsp;<i class="ver-t">￥</i><i class="firstMoney">{{this.firstPayment}}</i>
          </li>
           <li class="li">
            <span class="text">后续每期应还:</span>&nbsp;<i class="ver-t">￥</i><i class="firstMoney">{{this.terminalPayment}}</i>
          </li>
          <li class="fs12 cred mt10">实际扣款金额以银行账单为准</li>
        </ul>
      <!-- 输入卡号 -->
      <p class="fs12 c999 mt20 pl12">请使用本人银行卡进行操作</p>
       <form class="mt10 bgfff" id="form" @submit="noSubmit($event)">
        <label>
          <div class="li pr pl12 pr12 fs16 w borderbox">
              <input class="required cardNo input-text " name="cardno" _name="cardno" type="number" placeholder="请输入银行卡号" v-model="cardNo">
              <i class="del pa icon" style="top:10px;" @click="delInfo($event)">&#xe65d;</i>
          </div>
        </label>
      </form>
      <div class="mt20 pl12">
        <label for="agreement">
            <input class="hide" id="agreement" type="checkbox" :checked="protocal" v-model="protocal">
            <i class="icon agreement"></i>已阅读并同意
        </label><a class="c2eb944 pr protocal" @click="showProtocal" >《服务协议》</a>
      </div>
      <div class="pl12 pr12 mt30">
        <button class="w" id="submit" type="button" @click="next" :disabled="!protocal">
