        </button>
        <!-- <router-link class="mt30 w" tag="button" to="1.html">下一步</router-link> -->
      </div>
        <!-- 银行卡弹框 -->
      <div class="bank-tip" v-show="isShowBankTip">
        <h2>更换银行</h2>
        <div class="bt pt10 pb10 li" v-for="(item,index) of this.bankCardInfoTotal" :class='{bgf5f5f5:item.status=="unusable"}' @click="changeBank(item)">
          <div class="dis-inline ver-mid pl12">
            <img  :src='this.contextPath+"/static/bank-card-stage-static/assets/bank-logo/"+item.bankCode+".png"' width="30" height="30" alt="banklogo">
          </div>
          <div class="dis-inline ver-mid ml6">
            <p class="fs14 c333">{{item.bankName}}</p>
            <span class="fs12 c999"><i class="ml10" v-if='item.cardType == "CREDIT"'>信用卡</i></span>
          </div>
        </div>
        
      </div>
      <!-- 支持期数 -->
      <div class="bank-stage"  v-show="isShowBankStage">
        <h2 >更换期数</h2>
        <div class="c333 tc fs16" >
          <p class="sno bt" @click="changeStage(no)" v-for="no of  this.periodList[this.bankCardInfo.bankCode]">{{no}}期</p>
        </div>
