      <div class="verify bgfff tc " v-show="isShowVerify">
        <p class="tc h40 lh40 c333 fs16 bb">请输入验证码 <i class="icon pa fs24 close" @click="close">&#xe60e;</i></p>
        <p class="c666  fs16 tc mt10 pl12 pr12" id="smsText">验证码发成功</p>
        <form class="pr" name="form">
          <label for="verifyCode" class="clear bb bl br dis-inline inputBox" id="inputBox" @click="authorizedVerifycode">  <!-- 短信 -->
            <span class="fl span"></span>
            <span class="fl span"></span>
            <span class="fl span"></span>
            <span class="fl span"></span>
            <span class="fl span"></span>
            <span class="fl span"></span>
            <input type="tel" id="verifyCode" maxlength="6" v-model="smsCode">
          </label>
        </form>
        <button class="w button fs16 mt30 " type="button"  id="sendText" @click="smsSend">重新获取短信</button>
      </div>
      <!-- 验证码 结束-->
      <div class="bg-fixed" v-show="isShowMask" id="mask" @click="mask"></div>
      <div id="areaData"></div>
      <!-- 服务协议 -->
    <div class="pme-popup pme-protocol scroll" :class="{show:isProtocal}"><h2>服务协议<span class="close" @click="closeProtocal">同意</span></h2><div class="content-wrapper" tabindex="-1" style="height: 571px;"><div class="content"><h3>一、概述</h3><p>1、易宝支付《银行卡分期用户服务协议》（以下简称“本协议”）是您自愿与易宝支付有限公司（以下简称“易宝支付”）就易宝银行卡分期支付服务（以下简称“本服务”）的使用所签订的有效合约。您通过网络页面点击确认本协议或以其他方式选择接受本协议，即表示您与易宝支付已达成协议并同意接受本协议的全部约定内容。</p> <p>2、在您接受本协议之前，请您仔细阅读本协议的全部内容，如您不同意接受本协议的任意内容，或者无法准确理解相关条款含义的，请不要进行后续操作。如果您对本协议的条款有疑问的，请通过易宝支付客服渠道进行询问（易宝支付客服电话为4001-500-800），易宝支付将向您解释条款内容。</p> <p>您不应以未在交易单据中签名、签名不符、非本人意愿交易等原因要求易宝支付退款或承担其他责任。</p> <p>3、您同意，易宝支付有权随时对本协议内容进行单方面的变更，无需另行单独通知您；若您在本协议内容公告变更生效后继续使用本服务的，表示您已充分阅读、理解并接受变更修改后的协议内容，也将遵循变更修改后的协议内容使用本服务；若您不同意变更修改后的协议内容，您应在变更生效前停止使用本服务。</p><h3>二、双方权利义务</h3><p>1、您应确保您在使用本服务时的银行卡为您本人的银行卡，确保您使用银行卡的行为合法、有效，未侵犯任何第三方合法权益；否则因此造成易宝支付、商户、持卡人损失的，您应负责赔偿并承担全部法律责任。</p><p>2、您应妥善保管银行卡、卡号、密码、发卡行预留的手机号码等与银行卡有关的一切信息。如您遗失或泄漏前述信息，您应及时通知发卡行及/或易宝支付，以减少可能发生的损失。无论是否已通知发卡行及/或易宝支付，因您的原因所致损失需由您自行承担。</p><p>3、您在使用本服务时，应当认真确认交易信息，包括且不限于商品名称、数量、金额等，并按易宝支付业务流程发出约定的指令。您认可和同意：您发出的指令不可撤回或撤销，易宝支付一旦根据您的指令委托银行或第三方从银行卡中划扣资金给收款人，您不应以非本人意愿交易或其他任何原因要求易宝支付退款或承担其他责任。</p><p>4、您不应将本服务用于任何违反国家相关法律法规或本协议的目的。</p><p>5、您在对使用本服务过程中发出指令的真实性及有效性承担全部责任；您承诺，易宝支付依照您的指令进行操作的一切风险由您承担。</p><p>6、您认可并以易宝支付系统平台记录的数据为准。</p><p>7、同时您授权易宝支付有权留存您在易宝支付网站填写的相应信息，以供后续向您持续性地提供相应服务（包括但不限于将本信息用于向您推广、提供其他更加优质的产品或服务）。</p><p>8、出现下列情况之一的，易宝支付有权立即终止您使用易宝支付相关服务而无需承担任何责任：</p><p>&nbsp;（1）违反本协议的约定；</p><p>&nbsp;（2）将本服务用于非法目的；</p><p>&nbsp;（3）违反易宝支付/或其他关联公司网站的条款、协议、规则、通告等相关规定，而被上述任一网站终止提供服务的；</p><p>&nbsp;（4）易宝支付认为向您提供本服务存在风险的；</p><p>&nbsp;（5）您的银行卡无效、有效期届满或注销（如有）。</p><p>9、若您未违反本协议约定且因不能归责于您的原因，造成银行卡内资金通过本服务出现损失，且您未从中获益的，您可向易宝支付申请补偿。您应在知悉资金发生损失后及时通知易宝支付并按要求提交相关的申请材料和证明文件，您同意，您能否得到补偿及具体金额取决于易宝支付自身独立的判断，易宝支付承诺不会因此损害您的合法权益。</p><p>10、您同意并认可易宝支付最终的补偿行为并不代表前述资金损失应归责于易宝支付，亦不代表易宝支付须为此承担其他任何责任。您同意，易宝支付在向您支付补偿的同时，即刻取得您可能或确实存在的就前述资金损失而产生的对第三方的所有债权及其他权利，包括但不限于就上述债权向第三方追偿的权利，且您不再就上述已经让渡给易宝支付的债权向该第三方主张任何权利，亦不再就资金损失向易宝支付主张任何权利。</p><p>11、此外，在接受补偿的同时或之后，您从其它渠道挽回了前述资金损失的，或有新证据证明您涉