  </div>
</template>

<script>
let ERR_OK = 'success'; //返回成功码
import Abnormal from '@/components/abnormal.vue' 
export default {
  name: 'newUser',
  data () {
    return {
      contextPath:contextPath,
      protocal: true,//同意协议
      isProtocal:false, //服务协议显示
      bankCardInfo:[],//bankCardInfoTotal first
      bankStage:[],//bankStage first
      bankCardInfoTotal:[], //添加得银行卡信息 尾号和类型 [{"bankCode":"ABC","bankName":"ff银行","cardType":"信用卡","number":"2218"}]
      orderAmount:0,  //订单金额
      fee:0,  //手续费
      feeAfterSubsidy:'',  //减免后的手续费
      firstPayment:0,  //首期还款额
      terminalPayment:0,  //每期还款额
      cardNo:'',  //卡号
      isShowVerify:false ,//短信
      isShowMask:false ,//mask遮罩
      isShowBankTip:false ,//签约卡列表遮罩
      isShowBankStage:false ,//支持期数 
      bankCode:'',  //返回的 银行编码
      smsCode:'',  //监听短验
      period:'',//返回的 期数
      periodList:periodList ,//
      money:{},  //缓存已有卡信息账户
      isSure:'' //短验接口是否成功的标识，用来判定接下来是否调用确认支付接口
    }
  },
  watch:{
    cardNo(){
      var reg = /\d+/g;
      var con = this.cardNo
      if(!reg.test(con.substring(con.length-1))){
        this.cardNo = con.substring(0,con.length-1)
      }
      if(this.cardNo.length > 19){
        this.cardNo = this.cardNo.substring(0,19);
      }
    },
    smsCode(){   //输入到第六为掉支付接口
      var reg =  /\d+$/;  
      var con = this.smsCode
      if(!reg.test(con.substring(con.length-1))){
        this.smsCode = con.substring(0,con.length-1)
      }
      if(this.smsCode.length == 6){
        if(this.isSure != 'failed'){
          this.surePay();
        }
      }
    }
  },
  created() {
    // var a = this.changeStage()
    // alert(a)
    if(usableBankList && unusableBankList){
      this.bankCardInfoTotal = usableBankList.concat(unusableBankList)
    }else{
      this.bankCardInfoTotal = usableBankList ? usableBankList : unusableBankList
    } 
    if(!this.bankCardInfoTotal){
      console.log('银行列表为空')
      return false;
    }
    this.bankCardInfo = this.bankCardInfoTotal[0] //展示银行信息
    this.bankStage = this.periodList[this.bankCardInfoTotal[0].bankCode][0]//展示期数
    this.bankCode = this.bankCardInfo.bankCode
    this.period = this.periodList[this.bankCardInfoTotal[0].bankCode][0]//展示期数
    this.getMoneyInfo(token, this.bankCode,this.period) //加载页面请求钱信息
  },
   mounted() { 
    if(document.querySelector("#form")){
      User.form.validate("#form");
    }
  },
  methods:{
    getMoneyInfo(token,bankCode,period){  //每次更改银行或者分期期数目请求金额信息
      var param={}
        param.token = token
        param.bankCode = bankCode //银行编码
        param.period = period  //期数
        var totalMoney = JSON.parse(localStorage.getItem("money"));
          
        if(totalMoney && totalMoney[token+period+bankCode] ){
          var data = totalMoney[token+period+bankCode];
          // for(var item in data){
            this.orderAmount = data.orderAmount
            this.fee = data.fee
            this.feeAfterSubsidy = data.feeAfterSubsidy ? data.feeAfterSubsidy : '';
            this.firstPayment = data.firstPayment
            this.terminalPayment = data.terminalPayment
            this.bankCode = data.bankCode   //银行编码
            this.period = data.period //期数
          // }
          return false;
        }
      this.$http.get(Request.url+'/bankinstallment/ajax/rateinfo',{params:param}).then((res) => {
        var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
        if(data.bizStatus === ERR_OK){ //success
          this.orderAmount = data.orderAmount
          this.fee = data.fee
          this.feeAfterSubsidy = data.feeAfterSubsidy ? data.feeAfterSubsidy : '';
          this.firstPayment = data.firstPayment
          this.terminalPayment = data.terminalPayment
          this.bankCode = data.bankCode   //银行编码
          this.period = data.period //期数
          this.money[token+period+bankCode]={};
          this.money[token+period+bankCode].orderAmount = data.orderAmount
          this.money[token+period+bankCode].fee = data.fee
          this.money[token+period+bankCode].feeAfterSubsidy = data.feeAfterSubsidy? data.feeAfterSubsidy : '';
          this.money[token+period+bankCode].firstPayment = data.firstPayment
          this.money[token+period+bankCode].terminalPayment = data.terminalPayment
          this.money[token+period+bankCode].bankCode = data.bankCode
          this.money[token+period+bankCode].period = data.period
          localStorage.setItem("money",JSON.stringify(this.money));
        }else{ //fail
          this.$router.push({ path:'/Abnormal', component:Abnormal});//获取计费失败跳异常
          localStorage.repayurl = data.repayurl;
        }
      })
    },
    next() {
      if(!usableBankList){
        this.isShowMask = true;
          Tip.titleContentBtn('提示','该卡不可用,请更换银行卡',function(){
            dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
              this.isShowMask = false;
              dom.querySelector("#tip1").classList.add("none");
            }.bind(this),false);
          }.bind(this))
          return false;
      }
      var result = User.form.autoValidate("#form");
      if (result) {
        var param={}
          param.token = token
          param.period = this.period   //签约关系
          param.bankCode = this.bankCode  //期数
          param.cardNo = BASE64.encoder(this.cardNo);  //期数
      this.$http.get(Request.url+'/bankinstallment/ajax/cardno/order',{params:param}).then((res) => {
        var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
        if(data.bizStatus === ERR_OK){ //success
            if(data.cardState == 'SIGNED'){ //已签约
              this.isShowVerify = true //显示短信
              this.isShowMask = true  //显示短信
              this.smsSend();  ///发送短验
            }else{  //未签约
              this.setForm(data.urlInfo);
            }
        }else{ //fail
          this.isShowMask = true;
          Tip.titleContentBtn('提示',data.errormsg+"("+data.errorcode+")",function(){
            dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
              this.isShowMask = false;
              dom.querySelector("#tip1").classList.add("none");
            }.bind(this),false);
          }.bind(this))
        }
      })
      }
    },
    smsSend() {   //发送短验接口
       var param={}
          param.token = token
      dom.querySelector("#verifyCode").value = ''
       Array.from(dom.querySelectorAll("#inputBox .span")).forEach(function(item,index){
        return item.innerHTML = '';
      })
      this.$http.get(Request.url+'/bankinstallment/ajax/signed/sms',{params:param}).then((res) => {
        var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
        var target = dom.querySelector("#sendText");
        if(data.bizStatus === ERR_OK){ //success
          User.autoValidateCode(target);//获取验证码开始倒计时
          dom.querySelector("#smsText").innerHTML = '短信发送成功'
          this.isSure = 'success';
        }else if(data.bizStatus === "smsFailed"){ //fail
           dom.querySelector("#smsText").innerHTML = data.errormsg
           dom.querySelector("#sendText").classList.remove("timer");
           this.isSure = 'failed';
           dom.querySelector("#verifyCode").value = ''
        }else if(data.bizStatus === "failed"){  //5次上限
          window.location.href = this.contextPath+"/wap/fail/?token="+token+"&errorCode="+data.errorcode+"&errorMsg="+data.errormsg+"&needRepay="+true;
        }
      })
    },
    surePay() {   //确认支付接口
      var param={}
          param.token = token
          param.verifyCode = dom.querySelector("#verifyCode").value;   //短信参数
      this.$http.get(Request.url+'/bankinstallment/ajax/signed/confirm',{params:param}).then((res) => {
        var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
        if(data.bizStatus === ERR_OK){ //success
          var bank = {};
          bank.bankCode = this.bankCode
          bank.period = this.period
          localStorage.setItem("bank",JSON.stringify(bank))  //支付成功页需要用银行和编码请求金额
          window.location.href = this.contextPath+"/wap/query/result?token="+token;
        }else{ //fail
          if(data.bizStatus === 'smsFailed'){   //允许重发短验
            this.isShowMask = true; 
            this.isShowVerify = true;//短信输入错误
            dom.querySelector("#smsText").innerHTML = data.errormsg+"("+data.errorcode+")"; 
            this.smsCode = '';
            Array.from(dom.querySelectorAll("#inputBox .span")).forEach(function(item,index){
              return item.innerHTML = '';
            })
          }else{   //跳失败页
            window.location.href = this.contextPath+"/wap/fail/?token="+token+"&errorCode="+data.errorcode+"&errorMsg="+data.errormsg+"&needRepay="+true;
          }
        }
      })
    },
    setForm(obj) {   //创建表单提交数据到银联
      var charset = obj.charset
      var method = obj.method
      var params = obj.params
      var url = obj.url
      dom.querySelector("#areaData").innerHTML = '';
      var html = '';
      html += '<form name="form2" id="form2" action="'+url+'" method="'+method+'"  accept-charset="'+charset+'">'
     for(var item in params){
        html += '<input type="hidden" name="'+item+'" value="'+params[item]+'" />'
     }
     html += '<form>';
     dom.querySelector("#areaData").insertAdjacentHTML('beforeEnd',html)
     dom.querySelector("#form2").submit();
    },
    noSubmit(event){
      event.preventDefault();
      return false;
    }
  }
   
}
</script>
