<template>
  <div class="oldUser">
      <!-- 银行卡部分 -->
        <div class="bankCard bgfff jt icon pl12 bb pr" @click="showBank">   
          <div class="dis-inline ver-mid">
            <img  :src='this.contextPath+"/static/bank-card-stage-static/assets/bank-logo/"+this.bankCardInfo.bank.bankCode+".png"' width="30" height="30" alt="banklogo">
          </div>
          <div class="dis-inline ver-mid">
            <span class="fs14 c333">{{bankCardInfo.bank.bankName}} &nbsp;尾号{{bankCardInfo.cardNo}}</span>
          </div>
          <span class="fs14 fr cardtype" v-if="bankCardInfo.cardType === 'CREDIT'">信用卡</span>
        </div>
        
        <div class="bankCard bgfff icon pl12 bb pr" :class="{jt:this.periodList[this.bankCardInfoTotal[0].bank.bankCode].length > 1}" @click="showStage">   
          <div class="dis-inline fs14 stagetext">
            <span class="fs16 c666">分期期数</span>
          </div>
          <span class="stageNo fs16 c666" id="stage">{{this.bankStage}}期</span>
        </div>
        <!-- 钱 -->
        <ul class="info bgfff pl12 c666 pt10 pb10">
          <li class="li">
            <span class="text">订单金额:</span>&nbsp;<i class="ver-t">￥</i><i class="orderMoney">{{this.orderAmount}}</i>
          </li>
          <li class="li">
            <span class="text">手续费:</span>&nbsp;<i class="ver-t">￥</i><i class="handleMoney">{{this.fee}}</i>
          </li>
           <li class="li" v-show="this.feeAfterSubsidy">
            <span class="text">减免后手续费:</span>&nbsp;<i class="ver-t cff491f">￥</i><i class="handleMoney cff491f">{{this.feeAfterSubsidy}}</i>
          </li>
          <li class="li">
            <span class="text">首期还款:</span>&nbsp;<i class="ver-t">￥</i><i class="firstMoney">{{this.firstPayment}}</i> 
          </li>
          <li class="li">
            <span class="text">后续每期应还:</span>&nbsp;<i class="ver-t">￥</i><i class="firstMoney">{{this.terminalPayment}}</i>
          </li>
          <li class="fs12 cred mt10">实际扣款金额以银行账单为准</li>
        </ul>
        <div class="pl12 pr12 mt30">
          <button class="w" id="submit" type="button" @click="next">
            <i>下一步</i>
          </button>
        </div>
        <!-- 银行卡弹框 -->
      <div class="bank-tip" v-show="isShowBankTip">
        <h2>更换银行卡</h2>
        <div class="bt pt10 pb10 li" v-for="(item,index) of this.bankCardInfoTotal" :class='{bgf5f5f5:item.status=="unusable"}' @click="changeBank(item)">
          <div class="dis-inline ver-mid pl12">
            <img  :src='this.contextPath+"/static/bank-card-stage-static/assets/bank-logo/"+item.bank.bankCode+".png"' width="30" height="30" alt="banklogo">
          </div>
          <div class="dis-inline ver-mid ml6">
            <p class="fs14 c333">{{item.bank.bankName}}</p>
            <span class="fs12 c999">尾号{{item.cardNo}}<i class="ml10" v-if='item.cardType == "CREDIT"'>信用卡</i><i class="ml10">{{item.remark}}</i></span>
          </div>
        </div>

        <div class="bt add fs16 li" @click="addBankCard">
          <div class="dis-inline ver-mid">
            <i class="icon addSymbol fs20 c999">&#xe678;</i>
            <i class="c333 ml6">新增银行卡</i>
          </div>
        </div>
      </div>
      <!-- 支持期数 -->
      <div class="bank-stage" v-show="isShowBankStage">
        <h2 >更换期数</h2>
        <div class="c333 tc fs16" >
          <p class="sno bt" @click="changeStage(no)" v-for="no of this.periodList[this.bankCardInfo.bank.bankCode]">{{no}}期</p>
        </div>
      </div>
      <!-- 支持期数 结束-->
      <div class="verify bgfff tc " v-show="isShowVerify">
        <p class="tc h40 lh40 c333 fs16 bb">请输入验证码<i class="icon pa fs24 close" @click="close">&#xe60e;</i></p>
        <p class="c666  fs16 tc mt10 pl12 pr12" id="smsText">验证码发成功</p>
        <form class="pr" name="form">
          <label for="verifyCode" class="clear bb bl br dis-inline inputBox" id="inputBox" @click="authorizedVerifycode">  <!-- 短信 -->
            <span class="fl span"></span>
            <span class="fl span"></span>
            <span class="fl span"></span>
            <span class="fl span"></span>
            <span class="fl span"></span>
            <span class="fl span"></span>
            <input type="tel" id="verifyCode" maxlength="6" v-model="smsCode">
          </label>
        </form>
        <button class="w button fs16 mt30 timer" type="button" disabled="true" id="sendText" @click="smsSend">重新获取</button>
      </div>
      <!-- 验证码 结束-->
      <div class="bg-fixed" v-show="isShowMask" id="mask"  @click="mask"></div>
      
  </div>
</template>
<script>
let ERR_OK = 'success'; //返回成功码
import Abnormal from '@/components/abnormal.vue'
export default {
  name: 'oldUser',
  data () {
    return {
      contextPath:contextPath,
      bankCardInfo:[],//bankCardInfoTotal first
      signRelationId:'' ,//签约关系
      bankStage:[],//bankStage first
      bankCardInfoTotal:[], //添加得银行卡信息 尾号和类型 [{"bankCode":"ABC","bankName":"ff银行","cardType":"信用卡","number":"2218"}]
      orderAmount:0,  //订单金额
      fee:0,  //手续费
      feeAfterSubsidy:'',  //减免后的手续费
      firstPayment:0,  //首期还款额
      terminalPayment:0,  //每期还款额
      isShowVerify:false ,//短信
      isShowMask:false ,//mask遮罩
      isShowBankTip:false ,//签约卡列表遮罩
      isShowBankStage:false ,//支持期数 
      bankCode:'',  //返回的 银行编码
      smsCode:'',  //监听短验
      period:'',  //返回的 期数
      periodList:periodList, //
      money:{},  //缓存已有卡信息账户
      isSure:'' //短验接口是否成功的标识，用来判定接下来是否调用确认支付接口
    }
  },
  created() {
    if(usableSignRelationList && unusableSignRelationList){
      this.bankCardInfoTotal = usableSignRelationList.concat(unusableSignRelationList)
    }else{
      this.bankCardInfoTotal = usableSignRelationList ? usableSignRelationList : unusableSignRelationList
    } 
    if(!this.bankCardInfoTotal){
      console.log('银行卡列表为空')
      return false;
    }  
    this.bankCardInfo = this.bankCardInfoTotal[0] //展示银行信息
    this.bankStage = this.periodList[this.bankCardInfoTotal[0].bank.bankCode][0]//展示期数

    this.signRelationId = this.bankCardInfoTotal[0].signRelationId // 维护签约关系
    this.bankCode = this.bankCardInfo.bank.bankCode
    this.period = this.periodList[this.bankCardInfoTotal[0].bank.bankCode][0]//展示期数
    this.getMoneyInfo(token,this.bankCode,this.period) //加载页面请求钱信息
  },
  watch:{
    smsCode(){   //输入到第六为掉支付接口
      var reg = /\d+/g;
      var con = this.smsCode
      if(!reg.test(con.substring(con.length-1))){
        this.smsCode = con.substring(0,con.length-1)
      }
      if(this.smsCode.length == 6){
        if(this.isSure != 'failed'){
          this.surePay();
        }
      }
    }
  },
  methods:{
    getMoneyInfo(token,bankCode,period){  //每次更改银行或者分期期数目请求金额信息
      var param={}
        param.token = token
        param.bankCode = bankCode //银行编码
        param.period = period  //期数
        var totalMoney = JSON.parse(localStorage.getItem("money"));
          
        if(totalMoney && totalMoney[token+period+bankCode] ){
          var data = totalMoney[token+period+bankCode];
          // for(var item in data){
            this.orderAmount = data.orderAmount
            this.fee = data.fee
            this.feeAfterSubsidy = data.feeAfterSubsidy ? data.feeAfterSubsidy : '';
            this.firstPayment = data.firstPayment
            this.terminalPayment = data.terminalPayment
            this.bankCode = data.bankCode   //银行编码
            this.period = data.period //期数
          // }
          return false;
        }
      this.$http.get(Request.url+'/bankinstallment/ajax/rateinfo',{params:param}).then((res) => {
        var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
        if(data.bizStatus === ERR_OK){ //success
          this.orderAmount = data.orderAmount
          this.fee = data.fee
          this.feeAfterSubsidy = data.feeAfterSubsidy ? data.feeAfterSubsidy : '';
          this.firstPayment = data.firstPayment
          this.terminalPayment = data.terminalPayment
          this.bankCode = data.bankCode   //银行编码
          this.period = data.period //期数
          this.money[token+period+bankCode]={};
          this.money[token+period+bankCode].orderAmount = data.orderAmount
          this.money[token+period+bankCode].fee = data.fee
          this.money[token+period+bankCode].feeAfterSubsidy = data.feeAfterSubsidy? data.feeAfterSubsidy : '';
          this.money[token+period+bankCode].firstPayment = data.firstPayment
          this.money[token+period+bankCode].terminalPayment = data.terminalPayment
          this.money[token+period+bankCode].bankCode = data.bankCode
          this.money[token+period+bankCode].period = data.period
          localStorage.setItem("money",JSON.stringify(this.money));
        }else{ //fail
          this.$router.push({ path:'/Abnormal', component:Abnormal});//获取计费失败跳异常
          localStorage.repayurl = data.repayurl;
        }
      })
    },
    next(){  //下一步
      if(!usableSignRelationList){
        this.isShowMask = true;
          Tip.titleContentBtn('提示','该卡不可用,请更换银行卡',function(){
            dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
              this.isShowMask = false;
              dom.querySelector("#tip1").classList.add("none");
            }.bind(this),false);
          }.bind(this))
          return false;
      }
      var param={}
          param.token = token
          param.signRelationId = this.signRelationId   //签约关系
          param.period = this.period  //期数
          param.bankCode = this.bankCode  //期数
      this.$http.get(Request.url+'/bankinstallment/ajax/signed/order',{params:param}).then((res) => {
        var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
        if(data.bizStatus === ERR_OK){ //success
           this.isShowVerify = true //显示短信
           this.isShowMask = true  //显示短信
           this.smsSend();  ///发送短验
        }else{ //fail
          this.isShowMask = true;
          Tip.titleContentBtn('提示',data.errormsg+"("+data.errorcode+")",function(){
            dom.querySelector("#tip1 .close-btn").addEventListener("click",function(){
              this.isShowMask = false;
              dom.querySelector("#tip1").classList.add("none");
            }.bind(this),false);
          }.bind(this))
        }
      })
    },
    smsSend() {   //发送短验接口
      var param={}
          param.token = token
      dom.querySelector("#verifyCode").value = ''
      Array.from(dom.querySelectorAll("#inputBox .span")).forEach(function(item,index){
        return item.innerHTML = '';
      })

      this.$http.get(Request.url+'/bankinstallment/ajax/signed/sms',{params:param}).then((res) => {
        var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
        var target = dom.querySelector("#sendText");
        
        if(data.bizStatus === ERR_OK){ //success
            User.autoValidateCode(target);//获取验证码开始倒计时
            dom.querySelector("#smsText").innerHTML = '短信发送成功'
           this.isSure = 'success';
        }else if(data.bizStatus === "smsFailed"){ //fail
           dom.querySelector("#smsText").innerHTML = data.errormsg
           dom.querySelector("#sendText").classList.remove("timer");
           this.isSure = 'failed';
           dom.querySelector("#verifyCode").value = ''
        }else if(data.bizStatus === "failed"){ //5次上限
          window.location.href = this.contextPath+"/wap/fail/?token="+token+"&errorCode="+data.errorcode+"&errorMsg="+data.errormsg+"&needRepay="+true;
        }
      })
    },
    surePay() {   //确认支付接口
      var param={}
          param.token = token
          param.verifyCode = dom.querySelector("#verifyCode").value;   //短信参数
      this.$http.get(Request.url+'/bankinstallment/ajax/signed/confirm',{params:param}).then((res) => {
        var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body ;
        if(data.bizStatus === ERR_OK){ //success
          var bank = {};
          bank.bankCode = this.bankCode
          bank.period = this.period
          localStorage.setItem("bank",JSON.stringify(bank))  //支付成功页需要用银行和编码请求金额
          window.location.href = this.contextPath+"/wap/query/result?token="+token;
        }else{ //fail
          if(data.bizStatus === 'smsFailed'){   //允许重发短验
            this.isShowMask = true; 
            this.isShowVerify = true;//短信输入错误
            dom.querySelector("#smsText").innerHTML = data.errormsg+"("+data.errorcode+")"; 
            this.smsCode = '';
            Array.from(dom.querySelectorAll("#inputBox .span")).forEach(function(item,index){
              return item.innerHTML = '';
            })
          }else{   //跳失败页
            window.location.href = this.contextPath+"/wap/fail/?token="+token+"&errorCode="+data.errorcode+"&errorMsg="+data.errormsg+"&needRepay="+true;
          }
        }
      })
    },
    addBankCard(){
      window.location.href = this.contextPath+"/bankinstallment/wap/changecard?token="+token;
    }
  }
}
</script>
<style scoped lang="less">
  .cff491f{ color:#ff491f;}
  .bankCard{//  银行卡部分
    height:50px;
    line-height:50px;
    .cardtype{  //信用卡
      margin-right: 40px;
      color:#d8d8d8;
    }
    .stagetext{  //分期期数
      width:80px;
      font-weight:normal
    }
    &:active{
      background: #f5f5f5;
    }
  }
 .verify{   // 验证码
      width:88%;
      position: fixed;
      top:50%;
      left:50%;
      margin-left:-44%;
      margin-top:-40%;
      border-radius:4px;
      overflow: hidden;
      z-index:11;
      .close{
        right:0px;
        padding:0 10px;
        top:0px;
      }
      .button{
        height:44px;
        line-height:44px;
        border-radius:0px;
      }
      .timer{
        background: #f5f5f5;
        color:#999;
      }
      .h40{height:44px;}
      .lh40{line-height:44px;}
      .h30{height:30px;}
      .lh30{line-height:30px;}
      
      .inputBox{
        z-index: 11;
        border-radius:4px;
        overflow: hidden;
        margin-top:8px;
        #verifyCode{
          position: absolute;
          z-index: 11;
          left: -200px;
          top: 0;
          opacity: 0;
          width: 1px;
          height: 1px;
          font-size: 12px;
          -webkit-appearance: none;
        }
        
        .span{
          width:44px;height:44px;
          line-height: 44px;
          text-align: center;
          border-left:1px solid #ddd;
          border-top:1px solid #ddd;
          position: relative;
          z-index:11;
          &:first-of-type{border-radius:4px 0 0 0;border-left:none;}
        }

      }
    }
  .info{   //<!-- 钱 -->
      .orderMoney  ,.handleMoney ,.firstMoney{
        display:inline-block;
        vertical-align: top;
      }
      .li{
         height:36px; 
         line-height: 36px;
         .text{
          display:inline-block;
          width:110px;
          height:36px; 
          text-align: justify;
          overflow: hidden;
          &:after{
            content:'';
            width:100%;
            display: inline-block;
             overflow: hidden;
          }
        }
      }
    }
  .bank-tip{  //<!-- 银行卡弹框 -->
    .ml6{margin-left: 6px;}
    .li:active{background: #f5f5f5;}
      width:88%;
      max-height:80%;
      overflow-y: auto;
      background: #fff;
      position: fixed;
      top:50px;
      left:50%;
      margin-left:-44%;
      border-radius:4px;
      transition:opacity 0.5s;
      z-index:11;
      h2{
        padding:10px 13px;
        height:20px;
        font-weight: 700;
        font-size: 16px;
      }
      .add{
        padding:14px 12px;
        a{
          color:#666;
        }
      }
      .addSymbol{
        width:30px;height:30px;
        text-align: center;
        display: inline-block;
      }
    }
  .bank-stage{  //<!-- 支持期数 -->
    .li:active{
      background: #f5f5f5;
    }
    width:88%;
    max-height:80%;
    overflow-y: auto;
    background: #fff;
    position: fixed;
    top:50px;
    left:50%;
    margin-left:-44%;
    border-radius:4px;
    transition:opacity 0.5s;
    z-index:11;
    h2{
      padding:10px 13px;
      height:20px;
      font-weight: 700;
      font-size: 16px;
    }
    .sno{
      line-height: 40px;
      height: 40px;
      width:100%;

      &:active{
        background: #f5f5f5;
      }
    }
  }
  .jt:after{  //有 > jt
      position: absolute;
      right:12px;
      line-height: 46px;
      content:"\e65e";
      font-size:22px;
      color:#d8d8d8;
    }
</style>



