export default{
	install:function(Vue,opt){
		Vue.mixin({
			methods:{
				showProtocal() {
			      // 服务协议 
			      this.isShowMask = true;
			      this.isProtocal = true;
			    },
			    closeProtocal() {  // 服务协议 
			      this.isShowMask = false;
			      this.isProtocal = false;
			      this.protocal = true;
			    },
				showBank:function() {  //显示银行
				    this.isShowMask = true;
				    this.isShowBankTip = true;
			    },
			    showStage:function() {  //显示期数
			        this.isShowMask = true;
	      			this.isShowBankStage = true;
			    },
			    close:function(){
			    	this.isShowMask = false;
			    	this.isShowVerify = false;
			    	this.cleartimers()
			    },
			    mask:function(){
			    	this.isProtocal = false; // 服务协议 
			    	this.isShowMask = false;
			    	this.isShowVerify = false;
			    	this.isShowBankStage = false;
			    	this.isShowBankTip = false;
			    	this.cleartimers()
			    	this.hideTip()
			    },
			    cleartimers:function(){  //清定时器
			    	var timer = window.sessionStorage.getItem("timeId");
          			if(timer){
          				window.clearTimeout(timer)
          			}
			    },
			    hideTip(){  //错误弹框提示
					var tip1 = dom.querySelector("#tip1")
			    	if(tip1){
			    		tip1.classList.add("none");
			    	}
			    },
			    changeStage: function(no){  //更改期数
			      this.isShowMask = false
			      this.isShowBankStage = false
			      this.bankStage = no
			       if(mode == 'NEW'){
						this.bankCode = this.bankCardInfo.bankCode
			       }else{
						this.bankCode = this.bankCardInfo.bank.bankCode
			       }
			      
			      this.getMoneyInfo(token,this.bankCode,this.bankStage)
			    },
			    changeBank :function(item) {  //更改银行
			      if(mode == 'NEW'){
			      	this.bankStage = this.periodList[item.bankCode][0]   //分期数目维护
			      	this.bankCode = item.bankCode
					if(item.status == 'unusable' && usableBankList){   //既有可用得又有不可用得，就不可以点，若只有不可用的就可以点,未签约
				        return false;
				     }
			      }else{
			      	this.bankStage = this.periodList[item.bank.bankCode][0]   //分期数目维护
			      	this.bankCode = item.bank.bankCode
			      	if(item.status == 'unusable' && usableSignRelationList){   //既有可用得又有不可用得，就不可以点，若只有不可用的就可以点，已签约
				        return false;
				     }
			      }
			      this.isShowMask = false;
			      this.isShowBankTip = false;
			      this.bankCardInfo = item;
			      this.getMoneyInfo(token,this.bankCode,this.bankStage)
			    },
			    authorizedVerifycode:function() {   ////短信
			        var spans = document.querySelectorAll("#inputBox .span");//验证码每一个单元格
			        var verifyCodeField = dom.querySelector("#verifyCode");
			        verifyCodeField.addEventListener("input", function () {
			        	var reg =  /\d+$/;  
			        	var len = dom.querySelector("#verifyCode").value.length-1
			        	var val = dom.querySelector("#verifyCode").value
			            if(!reg.test(val.charAt(len))){
							dom.querySelector("#verifyCode").value = val.substring(0,len)
			            }
			            for (var i = 0, l = 6; i < l; i++) {
			                if(typeof this.value[i] != "undefined"){
			                	
			                  spans[i].innerHTML = this.value[i]
			                }else{
			                  spans[i].innerHTML = "";
			                }
			            }
			        }, false);
			    }
			}
		});
		Vue.prototype.delInfo=function(e){  //删除输入框内容
		  	var obj = e.target;
		  	obj.parentNode.querySelector(".input-text").value=''; 
		}
	}	
}



