import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'
import OldUser from '@/components/oldUser'   //老用户
import NewUser from '@/components/newUser'  //新用户
// import Processing from '@/components/processing'   //处理中
import Abnormal from '@/components/abnormal'  //异常
// import Success from '@/components/success'   //成功
// import Fail from '@/components/fail'   //失败

Vue.use(Router)
Vue.use(Resource)

const router = new Router({
  routes: [
    {
      path: '/OldUser',
      name: 'OldUser',   //老用户
      component: OldUser
    },
    {
      path: '/NewUser',  //新用户
      name: 'NewUser',
      component: NewUser
    },
    {
      path: '/Abnormal',  //异常
      name: 'Abnormal',
      component: Abnormal
    }
    
  ]
})
